import React, { useEffect, useState } from 'react';

function TimeVerify() {
    const [count, setCount] = useState(15)

    useEffect(() => {
        var counts = setInterval(() => {
            setCount(count => count - 1)
        }, 1000)
        if (count <= 0) {
            clearInterval(counts);
        }
        return () => {
            clearInterval(counts);
        }
    }, [count])

    return (
        <>
            <div style={{ display: (count <= 0 ? 'none' : 'block') }}
                className="mt-4 small"
            >G·ª≠i l·∫°i m√£ sau:
                <span style={{ color: 'rgb(13, 92, 182)' }}> {count}s</span>
            </div>

            <div className="small mt-4 mb-5" style={{ display: (count > 0 ? 'none' : 'block') }}>Kh√¥ng nh·∫≠n ƒë∆∞·ª£c?
                <span style={{
                    color: 'rgb(13, 92, 182)', cursor: 'pointer',
                }}
                    onClick={() => setCount(count => count + 30)}
                > G·ª≠i l·∫°i m√£</span>
            </div>
            <div className="small mt-2 mb-5" style={{ display: (count > 0 ? 'block' : 'none') }}>
                M√£ x√°c minh c√≥ hi·ªáu l·ª±c trong 15 ph√∫t
            </div>
        </>
    );
}

export default TimeVerify;